  width: 100%; }

.c-features {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style: none;
  margin: 0;
  margin-left: -15px;
  padding: 0;
  text-align: center; }

.c-features__item {
  border: 1px solid #437468;
  padding: 10px;
  background: #0d0d0d;
  margin-left: 15px;
  margin-bottom: 15px;
  max-width: 45%;
  transition: all 0.3s ease-out; }
  .c-features__item:hover {
    border: 1px solid #ff9650;
    transition: all 0.3s ease-out;
    cursor: pointer; }
  .c-features__item.is-active {
    border: 1px solid #ff9650; }

.c-features__content {
  overflow: hidden; }

.c-features__contentItem {
  visibility: hidden;
  opacity: 0;
  transition: all 0.3s ease-out;
  height: 0; }
  .c-features__contentItem.is-show {
    transition: all 0.3s ease-out;
    opacity: 1;
    visibility: visible;
    height: auto; }
  .c-features__contentItem h1 {
    margin-bottom: 30px;
    font-size: 16px; }

.c-curtain {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #00000040; }

.c-label {
  width: 90%;
  color: #f6f6f6;
  margin-bottom: 5px;
  text-align: left; }

.c-formRow {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px; }

.c-formRow__wrapper {
  left: 10px;
  margin-bottom: 5px;
  max-width: 97%;
  position: relative; }

.c-input {
  background: #2c463d;
  border: none;
  color: #f6f6f6;
  filter: none;
  font-size: 14px;
  padding: 12px 10px;
  width: 100%; }
  .c-input:required, .c-input:-moz-ui-invalid {
    box-shadow: none; }
    .c-input:required:focus, .c-input:-moz-ui-invalid:focus {
      border: none; }
  .c-input:-internal-autofill-previewed, .c-input:-internal-autofill-selected, .c-input:-webkit-autofill {
    background-color: #2c463d !important;
    border: none !important;
    -webkit-box-shadow: 0 0 0 100px #2c463d inset; }

.c-input__before {
  position: absolute;
  background: #2c463d;
  left: -10px;
  height: 32px;
  width: 10px; }
  .c-input__before:before {
    content: '';
    position: absolute;
    left: 0;
    bottom: -10px;
    border-color: transparent #2c463d transparent transparent;
    border-style: solid;
    border-width: 0 10px 10px 0;
    height: 0;
    width: 0; }

.c-input__after {
  position: absolute;
  background: #2c463d;
  right: -10px;
  height: 32px;
  width: 10px;
  top: 9px; }
  .c-input__after:before {
    content: '';
    position: absolute;
    right: 0;
    top: -10px;
    border-color: transparent transparent transparent #2c463d;
    border-style: solid;
    border-width: 10px 0 0 10px;
    height: 0;
    width: 0; }

.c-input__text {
  font-size: 14px;
  padding-left: 10px; }

.c-articles {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  max-width: 600px;
  padding: 15px;
  width: 100%; }
  @media screen and (min-width: 768px) {
    .c-articles {
      max-width: 840px; } }

.c-articleList__article {
  background: linear-gradient(180deg, rgba(22, 31, 26, 0.8) 0%, rgba(22, 31, 26, 0.8) 100%);
  flex: 0 1 45%;
  margin-bottom: 30px;
  margin-right: 15px;
  padding: 15px;
  position: relative; }

.c-articleList__title {
  font-size: 26px;
  margin-bottom: 20px; }

.c-articleList__footer {
  font-size: 10px;
  margin-top: 15px;
  text-align: right; }

.c-articleList___content {
  margin-bottom: 20px;
  max-height: 200px;
  max-width: 335px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

.c-table {
  border-collapse: collapse;
  border-spacing: 0;
  clear: both;
  empty-cells: show;
  width: 100%; }
  .c-table th {
    font-weight: 700;
    padding: 10px; }
  .c-table td {
    padding: 10px; }

.c-select {
  position: relative; }
  .c-select select {
    display: none; }

.c-select__selected {
  background: #2c463d;
  font-size: 14px;
  padding: 13px 16px; }
  .c-select__selected:after {
    border: 6px solid transparent;
    border-color: #f6f6f6 transparent transparent transparent;
    content: "";
    height: 0;
    position: absolute;
    right: 10px;
    top: 20px;
    transition: all 0.3s ease-out;
    width: 0; }
  .c-select__selected.c-select__arrow:after {
    border-color: transparent transparent #f6f6f6 transparent;
    top: 15px;
    transition: all 0.3s ease-out; }

.c-select__list {
  left: 0;
  list-style: none;
  margin: 0;
  max-height: 150px;
  opacity: 1;
  overflow-y: scroll;
  padding: 0;
  position: absolute;
  right: 0;
  top: 100%;
  transition: all 0.3s ease-out;
  visibility: visible;
  z-index: 99; }

.c-select__item {
  background: #2c463d;
  cursor: pointer;
  font-size: 14px;
  padding-left: 17px;
  padding: 12px;
  transition: all 0.3s ease-out; }
  .c-select__item:hover {
    background: #304840;
    transition: all 0.3s ease-out; }

.c-select__selected {
  color: #f6f6f6;
  padding: 13px 16px;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer; }

.c-select__same {
  background: #0d0d0d;
  padding: 11px;
  font-size: 14px;
  border: 1px solid #ff9650; }

.c-select__hidden {
  visibility: hidden;
  opacity: 0;
  transition: all 0.3s ease-out; }

.c-checkbox {
  cursor: pointer;
  display: flex;
  font-weight: 400;
  padding: 5px 0;
  text-transform: none;
  flex-wrap: wrap; }
  .c-checkbox.is-error {
    border: 2px solid red;
    padding: 10px; }
  .c-checkbox[disabled] {
    cursor: not-allowed; }
    .c-checkbox[disabled] .c-checkbox__span {
      background: #6afadc;
      cursor: not-allowed; }

.c-checkbox__over {
  display: inline-block; }
  .c-checkbox__over + .c-checkbox__text {
    margin-left: 10px; }

.c-checkbox__text {
  color: #f6f6f6; }
  .c-checkbox__text + .c-checkbox__over {
    margin-left: 10px; }

.c-checkbox__input {
  display: inline-block;
  position: absolute;
  visibility: hidden; }
  .c-checkbox__input + .c-checkbox__span {
    background-color: #0d0d0d;
    border: 1px solid #6afadc;
    cursor: pointer;
    display: inline-block;
    font-size: 12px;
    height: 16px;
    line-height: 16px;
    margin-top: -2px;
    position: relative;
    text-align: center;
    vertical-align: middle;
    width: 16px; }
    .is-error .c-checkbox__input + .c-checkbox__span {
      border: 1px solid red; }
  .c-checkbox__input:checked + .c-checkbox__span {
    border-color: #6afadc; }
    .c-checkbox__input:checked + .c-checkbox__span:before {
      background: #0d0d0d url("../../images/login/icons/checkmark.svg") no-repeat center;
      content: '';
      display: inline-block;
      height: 14px;
      left: 0;
      position: relative;
      top: 0;
      width: 14px; }

.c-validationMSG {
  font-size: 12px;
  color: red;
  padding-left: 10px;
  margin-bottom: 5px; }
  .c-checkbox .c-validationMSG {
    flex: 1 1 100%;
